        deleteApprovalVariablesRowByProcessType(["5"]);
    } else if (processModel.value.processType == "quintuple_approval") {
        addApprovalVariablesByProcessType([2, 3, 4, 5]);
    }
}

function addApprovalVariablesByProcessType(steps: Array<number>) {
    steps.forEach((step) => {
        if (!processModel.value.processVariables.some((variable) => variable.key == `supportingForm${step}`))
            processModel.value.processVariables.push({
                key: `supportingForm${step}`,
                value: "",
                type: "Screen",
                addedByUser: false,
            });
        if (!processModel.value.processVariables.some((variable) => variable.key == `checkerRoles${step}`)) {
