torFooter.vue"],"names":[],"mappings":";AA+DA;IACI,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,8BAA8B;IAC9B,kBAAkB;AACtB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,SAAS;IACT,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,+CAA+C;IAC/C,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,iBAAiB;IACjB,qBAAqB;AACzB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,SAAS;IACT,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,+CAA+C;IAC/C,kBAAkB;IAClB,qBAAqB;IACrB,cAAc;IACd,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,YAAY;AAChB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,SAAS;IACT,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,+CAA+C;IAC/C,kBAAkB;IAClB,cAAc;IACd,eAAe;IACf,sBAAsB;IACtB,qBAAqB;IACrB,gBAAgB;IAChB,yBAAyB;IACzB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,SAAS;IACT,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,+CAA+C;IAC/C,kBAAkB;IAClB,cAAc;IACd,eAAe;IACf,sBAAsB;IACtB,qBAAqB;IACrB,gBAAgB;IAChB,yBAAyB;IACzB,iBAAiB;AACrB","sourcesContent":["<template>\n    <div class=\"footer\">\n        <button class=\"previous-button\" v-if=\"props.state.stepper.currentStep != 1\" @click=\"props.handleBack\">PREVIOUS</button>\n        <label class=\"info-label\" v-if=\"infoLabelVisibility\"\n            >When you click create button, <b>{{ approvalCount }}-step approval</b> <b style=\"color: #007c77\">{{ bpmnFileName }}.bpmn</b> will be\n            created</label\n        >\n        <button\n            class=\"next-button\"\n            @click=\"props.handleNext\"\n            v-if=\"!saveButtonVisibility && !checkOutAndEditButtonVisibility\"\n            :disabled=\"!props.state.stepper.isTabValid\"\n        >\n            NEXT\n        </button>\n        <button class=\"next-button\" @click=\"props.handleSave\" v-if=\"saveButtonVisibility && !checkOutAndEditButtonVisibility\">SAVE</button>\n        <button class=\"discard-button\" @click=\"props.handleDiscard\" v-if=\"props.model.processState == 'UPDATE' && !checkOutAndEditButtonVisibility\">DISCARD</button>\n        <button class=\"edit-button\" @click=\"props.checkOutAndEdit\" v-if=\"checkOutAndEditButtonVisibility\">CHECK OUT AND EDIT</button>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { IProcessEditorState } from \"../../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { IProcessModel } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { computed, ref } from \"vue\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { IStudio } from \"../../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n\n\ninterface IProcessEditorFooterProps {\n    handleBack: () => void;\n    handleNext: () => void;\n    handleSave: () => void;\n    handleDiscard: () => void;\n    checkOutAndEdit: () => void;\n    resizeWizard: () => void;\n    state: IProcessEditorState;\n    model: IProcessModel;\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst studio = container.resolve<IStudio>(IStudio);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst props = defineProps<IProcessEditorFooterProps>();\nconst checkOutAndEditButtonVisibility = computed(() => !viewModel.studio.currentItem?.checkedOut);\nconst saveButtonVisibility = computed(() => props.state.stepper.currentStep == 5 || props.model.processType.length == 0 && props.state.stepper.currentStep == 1);\nconst infoLabelVisibility = computed(() => !props.model.usingExistingBpmnFile && !props.model.selectedExistingBpmnFileID.length && props.state.stepper.currentStep == 5);\nconst approvalCount = computed(\n    () =>\n        (Object.keys(props.model.processCheckerDefinition) as (keyof typeof props.model.processCheckerDefinition)[]).filter((key) => {\n            return props.model.processCheckerDefinition[key] != \"\";\n        }).length\n);\n\nconst bpmnFileName = ref(props.model.processFlowName || props.state.processFileName);\n\n\n</script>\n\n<style scoped>\n.footer {\n    width: 1000px;\n    height: 104px;\n    top: calc(100% - 104px);\n    border-radius: 0px 0px 4px 4px;\n    position: absolute;\n}\n\n.previous-button {\n    position: absolute;\n    left: 759px;\n    top: 32px;\n    width: 111px;\n    height: 40px;\n    border: none;\n    background: #cbcbcb 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    text-align: center;\n    font-family: \"Roboto\";\n    font-weight: 500;\n    user-select: none;\n}\n\n.info-label {\n    position: absolute;\n    top: 40px;\n    margin-left: 52px;\n    font-family: \"Roboto\";\n}\n\n.next-button {\n    position: absolute;\n    left: 882px;\n    top: 32px;\n    width: 78px;\n    height: 40px;\n    border: none;\n    background: #007c77 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    font-family: \"Roboto\";\n    color: #ffffff;\n    font-weight: 500;\n    user-select: none;\n}\n\n.next-button:disabled {\n    opacity: 0.5;\n}\n\n.discard-button {\n    position: absolute;\n    left: 39px;\n    top: 32px;\n    width: 102px;\n    height: 40px;\n    border: none;\n    background: #cbcbcb 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    color: #212121;\n    font-size: 15px;\n    letter-spacing: 0.02px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n    text-transform: uppercase;\n    user-select: none;\n}\n\n.edit-button {\n    position: absolute;\n    left: 767px;\n    top: 32px;\n    width: 191px;\n    height: 40px;\n    border: none;\n    background: #cbcbcb 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    color: #212121;\n    font-size: 15px;\n    letter-spacing: 0.02px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n    text-transform: uppercase;\n    user-select: none;\n}\n</style>\n"],"sourceRoot":""}]);
