     <select\n                class=\"combo start-screen-combo\"\n                :style=\"'color:' + (!!processMakerDefinition.makerScreenID ? ' black' : '#A1A1A1')\"\n                @change=\"handleMakerScreen\"\n            >\n                <option\n                    value=\"\"\n                    disabled\n                    :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processMakerDefinition.makerScreenID)\"\n                >\n                    Select\n                </option>\n                <option\n                    v-for=\"screen in props.modelsOfModule.screens\"\n                    :id=\"screen.ID\"\n                    :selected=\"screen.ID == processMakerDefinition.makerScreenID\"\n                    style=\"color: black !important\"\n                >\n                    {{ screen.name }}\n                </option>\n            </select>\n\n            <hr width=\"1\" size=\"500\" class=\"line first-line\" />\n\n            <p class=\"combo-label first-approver-text\">1st Approver Screen</p>\n            <select\n                class=\"combo first-approver-combo\"\n                :style=\"'color:' + (!!processCheckerDefinition.firstApproverScreenID.length ? ' black' : '#A1A1A1')\"\n                @change=\"handleCheckerScreen($event, 1)\"\n            >\n                <option\n                    value=\"\"\n                    disabled\n                    :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processCheckerDefinition.firstApproverScreenID)\"\n                >\n                    Select\n                </option>\n                <option\n                    v-for=\"screen in props.modelsOfModule.screens\"\n                    :id=\"screen.ID\"\n                    :selected=\"screen.ID == processCheckerDefinition.firstApproverScreenID\"\n                    style=\"color: black !important\"\n                >\n                    {{ screen.name }}\n                </option>\n            </select>\n\n            <div v-if=\"approverScreenCounter > 1\">\n                <p class=\"combo-label second-approver-text\">2nd Approver Screen</p>\n                <select\n                    class=\"combo second-approver-combo\"\n                    :style=\"'color:' + (!!processCheckerDefinition.secondApproverScreenID.length ? ' black' : '#A1A1A1')\"\n                    @change=\"handleCheckerScreen($event, 2)\"\n                >\n                    <option\n                        value=\"\"\n                        disabled\n                        :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processCheckerDefinition.secondApproverScreenID)\"\n                    >\n                        Select\n                    </option>\n                    <option\n                        v-for=\"screen in props.modelsOfModule.screens\"\n                        :id=\"screen.ID\"\n                        :selected=\"screen.ID == processCheckerDefinition.secondApproverScreenID\"\n                        style=\"color: black !important\"\n                    >\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i v-if=\"approverScreenCounter == 2  && !model.usingExistingBpmnFile && !model.selectedExistingBpmnFileID.length\" class=\"mdi mdi-delete-outline delete-icon second-delete-icon\" @click=\"decreaseApproverCounter()\">\n                </i>\n            </div>\n\n            <div v-if=\"approverScreenCounter > 2\">\n                <p class=\"combo-label third-approver-text\">3rd Approver Screen</p>\n                <select\n                    class=\"combo third-approver-combo\"\n                    :style=\"'color:' + (!!processCheckerDefinition.thirdApproverScreenID.length ? ' black' : '#A1A1A1')\"\n                    @change=\"handleCheckerScreen($event, 3)\"\n                >\n                    <option\n                        value=\"\"\n                        disabled\n                        :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processCheckerDefinition.thirdApproverScreenID)\"\n                    >\n                        Select\n                    </option>\n                    <option\n                        v-for=\"screen in props.modelsOfModule.screens\"\n                        :id=\"screen.ID\"\n                        :selected=\"screen.ID == processCheckerDefinition.thirdApproverScreenID\"\n                        style=\"color: black !important\"\n                    >\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i v-if=\"approverScreenCounter == 3  && !model.usingExistingBpmnFile && !model.selectedExistingBpmnFileID.length\" class=\"mdi mdi-delete-outline delete-icon third-delete-icon\" @click=\"decreaseApproverCounter()\">\n                </i>\n            </div>\n\n            <div v-if=\"approverScreenCounter > 3\">\n                <p class=\"combo-label fourth-approver-text\">4th Approver Screen</p>\n                <select\n                    class=\"combo fourth-approver-combo\"\n                    :style=\"'color:' + (!!processCheckerDefinition.fourthApproverScreenID.length ? ' black' : '#A1A1A1')\"\n                    @change=\"handleCheckerScreen($event, 4)\"\n                >\n                    <option\n                        value=\"\"\n                        disabled\n                        :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processCheckerDefinition.fourthApproverScreenID)\"\n                    >\n                        Select\n                    </option>\n                    <option\n                        v-for=\"screen in props.modelsOfModule.screens\"\n                        :id=\"screen.ID\"\n                        :selected=\"screen.ID == processCheckerDefinition.fourthApproverScreenID\"\n                        style=\"color: black !important\"\n                    >\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i v-if=\"approverScreenCounter == 4  && !model.usingExistingBpmnFile && !model.selectedExistingBpmnFileID.length\" class=\"mdi mdi-delete-outline delete-icon fourth-delete-icon\" @click=\"decreaseApproverCounter()\">\n                </i>\n            </div>\n\n            <div v-if=\"approverScreenCounter > 4\">\n                <p class=\"combo-label fifth-approver-text\">5th Approver Screen</p>\n                <select\n                    class=\"combo fifth-approver-combo\"\n                    :style=\"'color:' + (!!processCheckerDefinition.fifthApproverScreenID.length ? ' black' : '#A1A1A1')\"\n                    @change=\"handleCheckerScreen($event, 5)\"\n                >\n                    <option\n                        value=\"\"\n                        disabled\n                        :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == processCheckerDefinition.fifthApproverScreenID)\"\n                    >\n                        Select\n                    </option>\n                    <option\n                        v-for=\"screen in props.modelsOfModule.screens\"\n                        :id=\"screen.ID\"\n                        :selected=\"screen.ID == processCheckerDefinition.fifthApproverScreenID\"\n                        style=\"color: black !important\"\n                    >\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i\n                    v-if=\"approverScreenCounter == 5 && !model.usingExistingBpmnFile  && !model.usingExistingBpmnFile && !model.selectedExistingBpmnFileID.length\"\n                    class=\"mdi mdi-delete-outline delete-icon fifth-delete-icon\"\n                    @click=\"decreaseApproverCounter()\"\n                >\n                </i>\n            </div>\n\n            <button\n                v-if=\"approverScreenCounter < 5 && !model.usingExistingBpmnFile && !model.selectedExistingBpmnFileID.length\"\n                :class=\"'add-another-button approver-' + approverScreenCounter + '-add-button'\"\n                @click=\"increaseApproverCounter()\"\n            >\n                <i class=\"mdi mdi-plus plus-icon\"></i>\n                <div class=\"add-another-text\">Add Another</div>\n            </button>\n\n            <hr width=\"1\" size=\"500\" class=\"line second-line\" />\n\n            <p class=\"combo-label review-screen-text\">Review Screen</p>\n            <select\n                class=\"combo review-screen-combo\"\n                :style=\"'color:' + (props.model.reviewScreenID ? ' black' : '#A1A1A1')\"\n                @change=\"handleReviewScreen\"\n            >\n                <option value=\"\" disabled :selected=\"!props.modelsOfModule.screens.some((screen) => screen.ID == props.model.reviewScreenID)\">\n                    Select (Optional)\n                </option>\n                <option\n                    v-for=\"screen in props.modelsOfModule.screens\"\n                    :id=\"screen.ID\"\n                    :selected=\"screen.ID == props.model.reviewScreenID\"\n                    style=\"color: black !important\"\n                >\n                    {{ screen.name }}\n                </option>\n            </select>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, onBeforeMount, computed, onBeforeUnmount } from \"vue\";\nimport { ModifiedCallbackType } from \"../../../../../domain/model/editors/modifiedCallbackType\";\nimport { IModelsOfModule, IProcessEditorState } from \"../../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { IProcessModel } from \"@stechquick/algae/lib/qCloudTemp/process\";\n\nconst props = defineProps<{\n    model: IProcessModel;\n    modelsOfModule: IModelsOfModule;\n    state: IProcessEditorState;\n    modified: ModifiedCallbackType;\n}>();\n\nconst stepper = props.state.stepper;\nconst processMakerDefinition = computed(() => props.model.processMakerDefinition);\nconst processCheckerDefinition = computed(() => props.model.processCheckerDefinition);\nconst validations = {\n    isMakerScreenValid: false,\n    isFirstCheckerScreenValid: false,\n    isSecondCheckerScreenValid: false,\n    isThirdCheckerScreenValid: false,\n    isFourthCheckerScreenValid: false,\n    isFifthCheckerScreenValid: false,\n};\n\nonBeforeMount(() => {\n    checkTabValidation();\n});\n\nonBeforeUnmount(() => {\n    getApproverCount();\n})\n\nlet approverScreenCounter = ref(getApproverCount());\nfunction getApproverCount() {\n    let count = 1;\n    if (props.model.usingExistingBpmnFile) {\n        switch (props.model.processType) {\n            case \"quintuple_approval\":\n                count = 5;\n                break;\n            case \"quadruple_approval\":\n                count = 4;\n                break;\n            case \"triple_approval\":\n                count = 3;\n                break;\n            case \"double_approval\":\n                count = 2;\n                break;\n            default:\n                break;\n        }\n    } else if (processCheckerDefinition.value.fifthApproverScreenID != \"\") count = 5;\n    else if (processCheckerDefinition.value.fourthApproverScreenID != \"\") count = 4;\n    else if (processCheckerDefinition.value.thirdApproverScreenID != \"\") count = 3;\n    else if (processCheckerDefinition.value.secondApproverScreenID != \"\") count = 2;\n\n    setProcessType(count);\n    checkTabValidation();\n    return count;\n}\n\nfunction setProcessType(count: number) {\n    switch (count) {\n        case 1:\n            props.model.processType = \"single_approval\";\n            break;\n        case 2:\n            props.model.processType = \"double_approval\";\n            break;\n        case 3:\n            props.model.processType = \"triple_approval\";\n            break;\n        case 4:\n            props.model.processType = \"quadruple_approval\";\n            break;\n        case 5:\n            props.model.processType = \"quintuple_approval\";\n            break;\n        default:\n            break;\n    }\n}\n\nfunction increaseApproverCounter() {\n    if (approverScreenCounter.value < 5) approverScreenCounter.value++;\n    setProcessType(approverScreenCounter.value);\n    props.modified({ model: true });\n}\n\nfunction decreaseApproverCounter() {\n    switch (approverScreenCounter.value) {\n        case 2:\n            processCheckerDefinition.value.secondApproverScreenID = \"\";\n            break;\n        case 3:\n            processCheckerDefinition.value.thirdApproverScreenID = \"\";\n            break;\n        case 4:\n            processCheckerDefinition.value.fourthApproverScreenID = \"\";\n            break;\n        case 5:\n            processCheckerDefinition.value.fifthApproverScreenID = \"\";\n            break;\n        default:\n            break;\n    }\n    if (approverScreenCounter.value > 1) approverScreenCounter.value--;\n    setProcessType(approverScreenCounter.value);\n    props.modified({ model: true });\n}\n\nfunction handleMakerScreen($event: any) {\n    let selectedScreen = $event.target[$event.target.selectedIndex];\n\n    processMakerDefinition.value.makerScreenID = selectedScreen.id;\n    validations.isMakerScreenValid = !!processMakerDefinition.value.makerScreenID.length;\n    setTabValidation();\n    props.modified({ model: true });\n}\n\nfunction handleReviewScreen($event: any) {\n    let selectedScreen = $event.target[$event.target.selectedIndex];\n    props.model.reviewScreenID = selectedScreen.id;\n    props.modified({ model: true });\n}\n\nfunction handleCheckerScreen($event: any, number: number) {\n    let selectedScreen = $event.target[$event.target.selectedIndex];\n    switch (number) {\n        case 1:\n            processCheckerDefinition.value.firstApproverScreenID = selectedScreen.id;\n            validations.isFirstCheckerScreenValid = props.modelsOfModule.screens.some(screen => screen.ID == processCheckerDefinition.value.firstApproverScreenID);\n            setTabValidation();\n            break;\n        case 2:\n            processCheckerDefinition.value.secondApproverScreenID = selectedScreen.id;\n            validations.isSecondCheckerScreenValid = props.modelsOfModule.screens.some(screen => screen.ID == processCheckerDefinition.value.secondApproverScreenID);;\n            setTabValidation();\n            break;\n        case 3:\n            processCheckerDefinition.value.thirdApproverScreenID = selectedScreen.id;\n            validations.isThirdCheckerScreenValid = props.modelsOfModule.screens.some(screen => screen.ID == processCheckerDefinition.value.thirdApproverScreenID);;\n            setTabValidation();\n            break;\n        case 4:\n            processCheckerDefinition.value.fourthApproverScreenID = selectedScreen.id;\n            validations.isFourthCheckerScreenValid = props.modelsOfModule.screens.some(screen => screen.ID == processCheckerDefinition.value.fourthApproverScreenID);;\n            setTabValidation();\n            break;\n        case 5:\n            processCheckerDefinition.value.fifthApproverScreenID = selectedScreen.id;\n            validations.isFifthCheckerScreenValid = props.modelsOfModule.screens.some(screen => screen.ID == processCheckerDefinition.value.fifthApproverScreenID);;\n            setTabValidation();\n            break;\n        default:\n            break;\n    }\n    props.modified({ model: true });\n}\n\nfunction setTabValidation() {\n    stepper.isTabValid = validations.isMakerScreenValid && validations.isFirstCheckerScreenValid;\n    if ((props.model.usingExistingBpmnFile || props.model.processState == \"UPDATE\") && stepper.isTabValid) {\n        if (props.model.processType == \"double_approval\") {\n            stepper.