ponents/processVariableDataTable.vue\";\n\ninterface IProcessVariableProps {\n    editable?: boolean;\n    screens: Array<IModel>;\n    modified: ModifiedCallbackType;\n    resizeWizard: (height?: number) => void;\n    state: IProcessEditorState;\n    model: IProcessModel;\n}\n\nonBeforeMount(() => {\n    props.state.stepper.isTabValid = true;\n});\n\nconst props = defineProps<IProcessVariableProps>();\nconst localization = container.resolve<ILocalization>(ILocalization);\n\nconst newVariableName = ref(\"\");\nconst variableAlreadyExists = ref(false);\n\n\nfunction checkVariableNameExists() {\n    variableAlreadyExists.value = props.model.processVariables.find((variable) => variable.key == newVariableName.value) != undefined;\n}\n\nfunction addNewVariable() {\n    const variable: IProcessVariable = {\n        key: newVariableName.value,\n        value: \"\",\n        type: \"String\",\n        addedByUser: true,\n    };\n    if (variableAlreadyExists.value || newVariableName.value.length == 0) return;\n\n    props.model.processVariables.unshift(variable);\n    newVariableName.value = \"\";\n    props.modified({ model: true });\n}\n\n</script>\n\n<style scoped>\n.title {\n    position: absolute;\n    top: 118px;\n    left: 32px;\n    font-size: 24px;\n    font-family: \"Roboto\";\n    color: #212121;\n}\n\n.variable-count {\n    position: absolute;\n    top: 128px;\n    left: 255px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    color: #a1a1a1;\n}\n\n.new-variable-input {\n    position: absolute;\n    top: 118px;\n    left: 647px;\n    width: 220px;\n    height: 32px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px 0px 0px 4px;\n    padding: 7px 0px 7px 10px;\n    font-family: 'Roboto';\n    font-size: 14px;\n}\n\n.new-variable-input::placeholder {\n    position: absolute;\n    color: #a1a1a1;\n}\n\n.plus-icon {\n    position: absolute;\n    color: white;\n    top: 3px;\n    left: 12px;\n    font-size: 18px;\n    user-select: none;\n}\n\n.add-new-text {\n    position: absolute;\n    left: 32px;\n    top: 7px;\n    font-size: 13px;\n    font-weight: 500;\n    user-select: none;\n}\n\n.new-variable-button {\n    position: absolute;\n    left: 866px;\n    top: 118px;\n    font-size: 14px;\n    background: #007c77 0% 0% no-repeat padding-box;\n    border-radius: 0px 4px 4px 0px;\n    width: 101px;\n    height: 32px;\n    font-family: \"Roboto\";\n    border: 0px solid;\n    color: white;\n    padding: 0px;\n    user-select: none;\n}\n\n.disabled {\n    opacity: 0.5;\n    user-select: none;\n    pointer-events: none;\n    cursor: default;\n}\n</style>\n"],"sourceRoot":""}]);
