___CSS_LOADER_EXPORT___.push([module.id, "\n.process-editor[data-v-549807cb] {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n    transition: height 250ms ease-in-out;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/editors/processEditor/processEditor.vue"],"names":[],"mappings":";AA8cA;IACI,aAAa;IACb,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,sCAAsC;IACtC,gBAAgB;IAChB,yBAAyB;IACzB,kBAAkB;IAClB,oCAAoC;AACxC","sourcesContent":["<script setup lang=\"ts\">\nimport { IProcessEditorProps } from \"../../../../application/objects/editors/processEditorImpl\";\nimport { ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { ICreateModel } from \"../../../../domain/useCase/ICreateModel\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\n\nimport { IModule } from \"../../../../domain/model/models\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IProcessEditorUtil } from \"../../../../domain/useCase/IProcessEditorUtil\";\nimport { IModelsOfModule } from \"../../../../domain/presentation/editors/IProcessEditorModel\";\nimport ProcessEditorHeader from \"./components/processEditorHeader.vue\";\nimport ProcessEditorFooter from \"./components/processEditorFooter.vue\";\nimport ScreenSelectionTab from \"./tabs/screenSelectionTab.vue\";\nimport ServiceCallConfigurationTab from \"./tabs/serviceCallConfigurationTab.vue\";\nimport AdvancedSettingsTab from \"./tabs/advancedSettings.vue\";\nimport ProcessVariablesTab from \"./tabs/processVariablesTab.vue\";\nimport SummaryTab from \"./tabs/summaryTab.vue\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { IBpmnAdditionals } from '@stechquick/algae/lib/qCloudTemp/quickCloud';\n\ninterface IProps {\n    functions: IProcessEditorProps[\"functions\"];\n    callbacks: IProcessEditorProps[\"callbacks\"];\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst props = defineProps<IProps>();\nconst renderKey = ref(\"\");\nconst disableFinishButton = ref(false);\nconst title = ref(\"Process Wizard\");\nconst wizardHeight = ref(\"700px\");\n\nlet initialized = ref(false);\n\nconst processModel = ref(processEditorUtil.getEmptyProcessWizardModel(\"_\"));\nconst processEditorState = ref(processEditorUtil.getEmptyProcessEditorState());\nconst currentModule = ref({} as IModule);\nconst modelsOfModule = ref({\n    screens: [],\n    bpmns: [],\n} as IModelsOfModule);\nconst processVariablesEditable = ref(true);\n\nprops.functions.getModel = async () => {\n    if (processEditorState.value.stepper.currentStep == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    } else {\n        writeProcessModelDataToProcessVariablesTable();\n    }\n    return { model: processModel.value, state: processEditorState.value };\n};\n\nprops.functions.setModel = async (model, module, state) => {\n    currentModule.value = module;\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(module);\n    renderKey.value = CryptoHelper.CreateGuid();\n    processModel.value = model;\n    processEditorState.value = state;\n    initialized.value = true;\n    resizeWizard();\n};\n\nfunction checkDeletedScreens() {\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(viewModel.studio.currentItem?.parent as IModule);\n    let returnFlag = true;\n    let checkScreenID = processModel.value.processMakerDefinition.makerScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processMakerDefinition.makerScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.processCheckerDefinition.firstApproverScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processCheckerDefinition.firstApproverScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.processCheckerDefinition.secondApproverScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processCheckerDefinition.secondApproverScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.processCheckerDefinition.thirdApproverScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processCheckerDefinition.thirdApproverScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.processCheckerDefinition.fourthApproverScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processCheckerDefinition.fourthApproverScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.processCheckerDefinition.fifthApproverScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.processCheckerDefinition.fifthApproverScreenID = \"\";\n        returnFlag = false;\n    }\n\n    checkScreenID = processModel.value.reviewScreenID;\n    if (checkScreenID.length > 0 && modelsOfModule.value.screens.find((screen) => screen.ID == checkScreenID) == undefined) {\n        processModel.value.reviewScreenID = \"\";\n        returnFlag = false;\n    }\n\n    return returnFlag;\n}\n\nasync function handleNext() {\n    const stepper = processEditorState.value.stepper;\n    const currentPage = stepper.currentStep;\n    if (stepper.isTabValid) {\n        stepper.currentStep++;\n        stepper.isTabValid = false;\n        props.callbacks.modified({ state: true });\n    }\n    resizeWizard();\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn && stepper.currentStep == 1;\n\n    if (stepper.currentStep == 4) {\n        writeProcessModelDataToProcessVariablesTable();\n    } else if (currentPage == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n}\n\nfunction handleBack() {\n    if (processEditorState.value.stepper!.currentStep == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n    processEditorState.value.stepper!.currentStep--;\n    processEditorState.value.stepper!.isTabValid = false;\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile &&\n        processEditorState.value.selectedExistingBpmn &&\n        processEditorState.value.stepper.currentStep == 1;\n    props.callbacks.modified({ state: true });\n    resizeWizard();\n}\n\nfunction handleSave() {\n    const additionals = processEditorState.value.selectedExistingBpmn.modelAdditionals as IBpmnAdditionals;\n    const resizeWizardCondition = !processModel.value.usingExistingBpmnFile && !(additionals?.isAutoGenerated == false && additionals?.isModifiedByUser);\n    if (!checkDeletedScreens()) {\n        processEditorState.value.stepper.currentStep = 1;\n        if (resizeWizardCondition) {\n            resizeWizard();\n        }\n        return;\n    }\n    const processModelItem = viewModel.studio.currentItem!;\n    if (processModel.value.processState == \"CREATE\" && !processModel.value.usingExistingBpmnFile) {\n        const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.processFileName);\n        executor.execute(\n            async () => {\n                const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {\n                    name: processModel.value.processFlowName || processEditorState.value.processFileName,\n                    parentObjectId: currentModule.value.ID,\n                    modelType: \"bpmn\",\n                    modelBody: [{ key: \"bpmn\", model: bpmnFile }],\n                    modelAdditionals: { isAutoGenerated: true, isModifiedByUser: false },\n                });\n                processModel.value.processState = \"UPDATE\";\n                processModel.value.selectedExistingBpmnFileID = createdItem.ID;\n                modelsOfModule.value.bpmns.push(createdItem);\n                disableFinishButton.value = true;\n                await studio.save(processModelItem);\n                processEditorState.value.stepper.currentStep = 1;\n                resizeWizard();\n                studio.openItem(createdItem, { fromHistory: false, noFocus: true });\n            },\n            { loading: true }\n        );\n    } else {\n        disableFinishButton.value = true;\n        executor.execute(\n            async () => {\n                disableFinishButton.value = true;\n                processModel.value.processState = \"UPDATE\";\n                await studio.save(processModelItem);\n                processEditorState.value.stepper.currentStep = 1;\n\n                if (resizeWizardCondition) {\n                    resizeWizard();\n                }\n            },\n            { loading: true }\n        );\n    }\n    props.callbacks.modified({ state: true });\n}\n\nfunction handleDiscard() {\n    executor.execute(\n        async () => {\n            await studio.release(viewModel.studio.currentItem!);\n        },\n        { loading: true }\n    );\n}\n\nfunction checkOutAndEdit() {\n    executor.execute(async () => {\n        await studio.checkout(viewModel.studio.currentItem!)\n    }, { loading: true });\n    if (processEditorState.value.advancedVariablesTabSelected && processModel.value.processState == \"UPDATE\" && processModel.value.processType) processEditorState.value.stepper.currentStep = 4;\n    else processEditorState.value.stepper.currentStep = 1;\n    if(processModel.value.processType) resizeWizard();\n}\n\nfunction resizeWizard(height?: number) {\n    if (height) {\n        wizardHeight.value = height + \"px\"\n    }\n    else if (\n        processEditorState.value.stepper.currentStep == 4 ||\n        processEditorState.value.stepper.currentStep == 5 ||\n        !viewModel.studio.currentItem?.checkedOut ||\n        processModel.value.processType.length == 0\n    ) {\n        wizardHeight.value = \"886px\";\n    } else {\n        wizardHeight.value = \"700px\";\n    }\n}\n\nfunction writeProcessModelDataToProcessVariablesTable() {\n    if (!processModel.value.usingExistingBpmnFile && processModel.value.processState != \"UPDATE\") {\n        addApprovalVariablesIntoTableRows();\n    }\n    processModel.value.processVariables.forEach((variable) => {\n        switch (variable.key) {\n            case \"isRunAfterApprovalEnable\":\n                variable.value = processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable;\n                break;\n            case \"runAfterApprovalApi\":\n                variable.value = processModel.value.runAfterApprovalDefinition.runAfterApprovalApi;\n                break;\n            case \"runAfterApprovalMethod\":\n                variable.value = processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                variable.value = processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable;\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                variable.value = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                variable.value = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                variable.value = processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable;\n                break;\n            case \"runAfterRejectedApi\":\n                variable.value = processModel.value.runAfterRejectedDefinition.runAfterRejectedApi;\n                break;\n            case \"runAfterRejectedMethod\":\n                variable.value = processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod;\n                break;\n            case \"useHierarchicalProcess\":\n                variable.value = processModel.value.processMakerDefinition.useHierarchicalProcess;\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                variable.value = processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit;\n                break;\n            case \"instUnitHierarchy\":\n                variable.value = processModel.value.processMakerDefinition.instUnitHierarchy;\n                break;\n            case \"startForm\":\n                variable.value = processModel.value.processMakerDefinition.makerScreenID;\n                break;\n            case \"supportingForm\":\n                variable.value = processModel.value.processCheckerDefinition.firstApproverScreenID;\n                break;\n            case \"supportingForm2\":\n                variable.value = processModel.value.processCheckerDefinition.secondApproverScreenID;\n                break;\n            case \"supportingForm3\":\n                variable.value = processModel.value.processCheckerDefinition.thirdApproverScreenID;\n                break;\n            case \"supportingForm4\":\n                variable.value = processModel.value.processCheckerDefinition.fourthApproverScreenID;\n                break;\n            case \"supportingForm5\":\n                variable.value = processModel.value.processCheckerDefinition.fifthApproverScreenID;\n                break;\n            case \"reviewForm\":\n                variable.value = processModel.value.reviewScreenID;\n                break;\n            default:\n                break;\n        }\n    });\n}\n\nfunction writeProcessVariablesTableDataToProcessModel() {\n    processModel.value.processVariables.forEach((variable) => {\n        switch (variable.key) {\n            case \"isRunAfterApprovalEnable\":\n                processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable = variable.value as boolean;\n                break;\n            case \"runAfterApprovalApi\":\n                processModel.value.runAfterApprovalDefinition.runAfterApprovalApi = variable.value as string;\n                break;\n            case \"runAfterApprovalMethod\":\n                processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod = variable.value as string;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable = variable.value as boolean;\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi = variable.value as string;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod = variable.value as string;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable = variable.value as boolean;\n                break;\n            case \"runAfterRejectedApi\":\n                processModel.value.runAfterRejectedDefinition.runAfterRejectedApi = variable.value as string;\n                break;\n            case \"runAfterRejectedMethod\":\n                processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod = variable.value as string;\n                break;\n            case \"useHierarchicalProcess\":\n                processModel.value.processMakerDefinition.useHierarchicalProcess = Boolean(variable.value);\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit = Boolean(variable.value);\n                break;\n            case \"instUnitHierarchy\":\n                processModel.value.processMakerDefinition.instUnitHierarchy = variable.value as string;\n                break;\n            case \"startForm\":\n                processModel.value.processMakerDefinition.makerScreenID = variable.value as string;\n                break;\n            case \"supportingForm\":\n                processModel.value.processCheckerDefinition.firstApproverScreenID = variable.value as string;\n                break;\n            case \"supportingForm2\":\n                processModel.value.processCheckerDefinition.secondApproverScreenID = variable.value as string;\n                break;\n            case \"supportingForm3\":\n                processModel.value.processCheckerDefinition.thirdApproverScreenID = variable.value as string;\n                break;\n            case \"supportingForm4\":\n                processModel.value.processCheckerDefinition.fourthApproverScreenID = variable.value as string;\n                break;\n            case \"supportingForm5\":\n                processModel.value.processCheckerDefinition.fifthApproverScreenID = variable.value as string;\n                break;\n            case \"reviewForm\":\n                processModel.value.reviewScreenID = variable.value as string;\n                break;\n            default:\n                break;\n        }\n    });\n}\n\nfunction addApprovalVariablesIntoTableRows() {\n    if (processModel.value.processType == \"single_approval\") {\n        deleteApprovalVariablesRowByProcessType([\"2\", \"3\", \"4\", \"5\"]);\n    } else if (processModel.value.processType == \"double_approval\") {\n        addApprovalVariablesByProcessType([2]);\n        deleteApprovalVariablesRowByProcessType([\"3\", \"4\", \"5\"]);\n    } else if (processModel.value.processType == \"triple_approval\") {\n        addApprovalVariablesByProcessType([2, 3]);\n        deleteApprovalVariablesRowByProcessType([\"4\", \"5\"]);\n    } else if (processModel.value.processType == \"quadruple_approval\") {\n        addApprovalVariablesByProcessType([2, 3, 4]);\n        deleteApprovalVariablesRowByProcessType([\"5\"]);\n    } else if (processModel.value.processType == \"quintuple_approval\") {\n        addApprovalVariablesByProcessType([2, 3, 4, 5]);\n    }\n}\n\nfunction addApprovalVariablesByProcessType(steps: Array<number>) {\n    steps.forEach((step) => {\n        if (!processModel.value.processVariables.some((variable) => variable.key == `supportingForm${step}`))\n            processModel.value.processVariables.push({\n                key: `supportingForm${step}`,\n                value: \"\",\n                type: \"Screen\",\n                addedByUser: false,\n            });\n        if (!processModel.value.processVariables.some((variable) => variable.key == `checkerRoles${step}`)) {\n            const checkerRole =\n                processEditorState.value.processName +\n                (step == 2\n                    ? \".secondChecker\"\n                    : step == 3\n                        ? \".thirdChecker\"\n                        : step == 4\n                            ? \".fourthChecker\"\n                            : step == 5\n                                ? \".fifthChecker\"\n                                : \".checker\");\n            processModel.value.processVariables.push({\n                key: `checkerRoles${step}`,\n                value: checkerRole,\n                type: \"String\",\n                addedByUser: false,\n            });\n        }\n    });\n}\n\nfunction deleteApprovalVariablesRowByProcessType(steps: Array<string>) {\n    processModel.value.processVariables = processModel.value.processVariables.filter(\n        (row) =>\n            !(\n                steps.includes(row.key.charAt(row.key.length - 1)) &&\n                ((row.key.startsWith(\"checkerRoles\") && row.key.length == 13) || (row.key.startsWith(\"supportingForm\") && row.key.length == 15))\n            )\n    );\n}\n</script>\n<template>\n    <div class=\"process-editor\" :style=\"'height:' + wizardHeight\" v-if=\"initialized\" :key=\"renderKey\">\n        <ProcessEditorHeader :state=\"processEditorState\" :title=\"title\" :process-state=\"processModel.processState\" />\n        <div v-if=\"!viewModel.studio.currentItem?.checkedOut\">\n            <SummaryTab :model=\"processModel\" :state=\"processEditorState\" :screens=\"modelsOfModule.screens\"\n                :resize-wizard=\"resizeWizard\" :modified=\"props.callbacks.modified\" />\n        </div>\n        <div v-else>\n            <ScreenSelectionTab\n                v-if=\"processEditorState.stepper.currentStep === 1 && processModel.processType.length != 0\"\n                :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\" />\n            <ServiceCallConfigurationTab\n                v-if=\"processEditorState.stepper.currentStep === 2 && processModel.processType.length != 0\"\n                :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\" />\n            <AdvancedSettingsTab\n                v-if=\"processEditorState.stepper.currentStep === 3 && processModel.processType.length != 0\"\n                :model=\"processModel\" :state=\"processEditorState\" :modified=\"props.callbacks.modified\" />\n            <ProcessVariablesTab\n                v-if=\"processEditorState.stepper.currentStep == 4 || processModel.processType.length == 0 && processEditorState.stepper.currentStep == 1\"\n                :editable=\"true\" :state=\"processEditorState\" :screens=\"modelsOfModule.screens\"\n                :modified=\"props.callbacks.modified\" :resize-wizard=\"resizeWizard\" :model=\"processModel\" />\n            <SummaryTab v-if=\"processEditorState.stepper.currentStep === 5 && processModel.processType.length != 0\"\n                :model=\"processModel\" :state=\"processEditorState\" :screens=\"modelsOfModule.screens\"\n                :modified=\"props.callbacks.modified\" :resize-wizard=\"resizeWizard\" />\n        </div>\n        <div></div>\n        <ProcessEditorFooter :handle-next=\"handleNext\" :handle-back=\"handleBack\" :handle-save=\"handleSave\"\n            :handle-discard=\"handleDiscard\" :resize-wizard=\"resizeWizard\" :state=\"processEditorState\"\n            :model=\"processModel\" :check-out-and-edit=\"checkOutAndEdit\" />\n    </div>\n</template>\n<style scoped>\n.process-editor {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n    transition: height 250ms ease-in-out;\n}\n</style>\n"],"sourceRoot":""}]);
