ps.state.advancedVariablesTabSelected\">\n            <div>\n                <label class=\"child-title\">Process Screens</label>\n                <div class=\"process-screen-content\" :style=\"{ height: processScreensContentHeight }\">\n                    <div v-for=\"(v, k) in processScreens\">\n                        <div class=\"sub-content\" v-if=\"v != '-'\">\n                            <label class=\"ct-header\">{{ k }}</label\n                            ><br />\n                            <i class=\"mdi mdi-palette ct-icon\"></i>\n                            <label class=\"ct-text\">{{ v }}</label>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div>\n                <label class=\"child-title\">Service Call</label>\n                <div class=\"service-call-content\">\n                    <br /><label class=\"service-call-subtitle\">After All Approvals Done</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterAllApprovalsDone\">\n                        <label class=\"ct-header\">{{ k }}</label\n                        ><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                    <hr class=\"line\" />\n                    <label class=\"service-call-subtitle\">After Process Rejected</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterProcessRejected\">\n                        <label class=\"ct-header\">{{ k }}</label\n                        ><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                    <hr class=\"line\" />\n                    <label class=\"service-call-subtitle\">After Changes Advised</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterChangesAdvised\">\n                        <label class=\"ct-header\">{{ k }}</label\n                        ><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                </div>\n            </div>\n            <div>\n                <label class=\"child-title\">Advanced Settings</label>\n                <div class=\"advanced-settings-content\">\n                    <div class=\"sub-content\" v-for=\"(v, k) in advancedSettings\">\n                        <label class=\"ct-header\">{{ k }}</label\n                        ><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <ProcessVariableDataTable v-else :top=\"(props.model.processType && summaryTitleVisibility ? '221px' : '')\"\n            :editable=\"false\" :model=\"props.model\" :modified=\"props.modified\" :resize-wizard=\"props.resizeWizard\"\n            :screens=\"props.screens\" :state=\"props.state\" />\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { computed, ref } from 'vue';\nimport { IProcessModel } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IProcessEditorState } from \"../../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { ModifiedCallbackType } from \"../../../../../domain/model/editors/modifiedCallbackType\";\nimport { IProcessEditorUtil } from '../../../../../domain/useCase/IProcessEditorUtil';\nimport ProcessVariableDataTable from '../components/processVariableDataTable.vue';\n\ninterface ISummaryProps {\n    model: IProcessModel;\n    state: IProcessEditorState;\n    screens: Array<IModel>;\n    resizeWizard: (height?: number) => void;\n    modified: ModifiedCallbackType;\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst summaryTitleVisibility = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst props = defineProps<ISummaryProps>();\n\nconst processScreens = ref({\n    \"Start Screen\": getScreenName(props.model.processMakerDefinition.makerScreenID) || \"-\",\n    \"1st Approver Screen\": getScreenName(props.model.processCheckerDefinition.firstApproverScreenID) || \"-\",\n    \"2nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.secondApproverScreenID) || \"-\",\n    \"3nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.thirdApproverScreenID) || \"-\",\n    \"4nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.fourthApproverScreenID) || \"-\",\n    \"5nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.fifthApproverScreenID) || \"-\",\n    \"Review Screen\": getScreenName(props.model.reviewScreenID) || \"-\",\n});\n\nconst serviceCallAfterAllApprovalsDone = ref({\n    \"Service URL\": props.model.runAfterApprovalDefinition.isRunAfterApprovalEnable ? props.model.runAfterApprovalDefinition.runAfterApprovalApi : \"-\",\n    \"Service HTTP Method\": props.model.runAfterApprovalDefinition.isRunAfterApprovalEnable\n        ? props.model.runAfterApprovalDefinition.runAfterApprovalMethod\n        : \"-\",\n});\n\nconst serviceCallAfterProcessRejected = ref({\n    \"Service URL\": props.model.runAfterRejectedDefinition.isRunAfterRejectedEnable ? props.model.runAfterRejectedDefinition.runAfterRejectedApi : \"-\",\n    \"Service HTTP Method\": props.model.runAfterRejectedDefinition.isRunAfterRejectedEnable\n        ? props.model.runAfterRejectedDefinition.runAfterRejectedMethod\n        : \"-\",\n});\n\nconst serviceCallAfterChangesAdvised = ref({\n    \"Service URL\": props.model.r