        <div v-else class=\"row-data-type data-type-editable\">\n                    <select v-if=\"variable.addedByUser\" class=\"data-type-combo\" style=\"color: #212121;\"\n                        v-model=\"variable.type\" @change=\"handleDataTypeChange(variable)\">\n                        <option v-for=\"dataType in dataTypes\" :selected=\"dataType == variable.type\">{{ dataType }}\n                        </option>\n                    </select>\n                    <input v-else type=\"text\" class=\"data-type-combo\" style=\"background-color:#e3e3e3\"\n                        :id=\"variable.key\" :name=\"variable.key\" :value=\"variable.type\" :readonly=\"!variable.addedByUser\"\n                        :disabled=\"!variable.addedByUser\" />\n                </div>\n\n                <div v-if=\"!props.editable\" class=\"row-current-value current-value\">\n                    {{ screens.find((screen) => screen.ID == variable.value)?.name || variable.value || \"-\" }}\n                </div>\n                <div v-else class=\"row-current-value current-value-editable\">\n                    <input\n                        v-if=\"variable.type == 'String' && variable.key != 'instUnitHierarchy' || variable.type === 'Number'\"\n                        type=\"text\" class=\"current-value-input\" @input=\"modifyModel\" :id=\"variable.key\"\n                        :name=\"variable.key\" v-model=\"variable.value\" />\n                    <select v-if=\"variable.type == 'String' && variable.key == 'instUnitHierarchy'\"\n                        class=\"current-value-input\" v-model=\"variable.value\"\n                        :style=\"'color:' + (String(variable.value).length == 0 ? '#A1A1A1' : 'black')\">\n                        <option style=\"color: black !important\">SAME</option>\n                        <option style=\"color: black !important\">PARENT</option>\n                    </select>\n                    <select v-if=\"variable.type == 'Boolean'\" class