ue\";\n    modifyModel();\n}\n\nfunction modifyModel() {\n    props.modified({ model: true });\n}\n</script>\n\n<style scoped>\n.data-table {\n    position: absolute;\n    top: 182px;\n    left: 32px;\n    width: 936px;\n    height: 556px;\n}\n\n.table-header-text {\n    top: 11px;\n    color: #373737;\n    font-size: 13px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n}\n\n.table-header {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    width: 100%;\n    height: 40px;\n    background: #e3e3e3 0% 0% no-repeat padding-box;\n}\n\n.header-parameter {\n    position: absolute;\n    left: 12px;\n}\n\n.header-dataType {\n    position: absolute;\n    left: 193px;\n}\n\n.header-currentValue {\n    position: absolute;\n    left: 395px;\n}\n\n.header-defaultValue {\n    position: absolute;\n    left: 663px;\n}\n\n.table-body {\n    position: absolute;\n    top: 40px;\n    width: 936px;\n    border: none;\n    overflow-x: hidden;\n    overflow-y: auto;\n}\n\n.table-body::-webkit-scrollbar {\n    width: 4px;\n}\n\n.table-body::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.table-body::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.table-row {\n    width: 100%;\n    height: 44px;\n    display: flex;\n}\n\n.row-parameter {\n    margin-top: 14px;\n    margin-left: 12px;\n    font-size: 12px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 140px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-data-type {\n    margin-left: 41px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 154px;\n    display: inline-block;\n}\n\n.data-type {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.data-type-editable {\n    margin-top: 7px;\n}\n\n.data-type-combo {\n    width: 154px;\n    height: 30px;\n 